body {
  margin: 0px;
  background-color: rgba(0, 110, 255, 0.068);
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
::-webkit-scrollbar {
  height: 5px;
  width: 5px;
  scroll-behavior: smooth;
  background: rgba(62, 62, 51, 0);
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background: gray;
}

h1 {
  text-align: center;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 600;
  /* display: none; */
}

p {
  margin-top: 0px;
  margin-bottom: 0px;
}

#chatlog {
  overflow-y: scroll;
  height: 85%;
}

#chatbox {
  height: 90%;
  position: absolute;
  width: 100%;
  bottom: 0;
}

#chatbox .chatbar {
  position: absolute;
  bottom: 0;
  width: 100%;
  justify-content: center;
  height: 15%;
  box-shadow: 0 1px 6px rgb(32 33 36 / 28%);
  background: white;
}

.jfl {
  display: flex;
  justify-content: center;
  width: 80%;
  height: 50px;
  margin: auto;
  box-shadow: 0 1px 6px rgb(32 33 36 / 28%);
  padding: 0px 10px 0px 10px;
  border-radius: 8px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

input {
  width: 100%;
  border: none;
  outline: 0;
  font-size: 17px;
  font-weight: 600;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

button {
  border: none;
  background: transparent;

}

.user {
  padding: 20px 15px 20px 15px;
  /* background: white; */
  font-size: 17px;
  font-weight: 600;
  width: 80%;
  margin: auto;
  margin-top: 15px;
  margin-bottom: 15px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  animation: popup 100ms;
}

.bot {
  padding: 20px 15px 20px 15px;
  background: white;
  /* width: -webkit-fill-available; */
  /* border: 1px solid rgba(0, 0, 0, .1); */
  text-align: left;
  width: 80%;
  margin: auto;
  margin-top: 15px;
  margin-bottom: 15px;
  border-radius: 8px;
  color: #000000;
  animation: popup 200ms;
  box-shadow: 0 1px 6px rgb(32 33 36 / 28%);
}
@keyframes popup {
  0%{
      opacity: 0;
  }
  100%{
      opacity: 1;
  }
}

.bot a {
  box-shadow: 0 1px 6px rgb(32 33 36 / 28%);
  padding: 10px;
  margin: 0px 0px 0px 0px;
  text-decoration: none;
  color: rgb(0, 140, 255);
  font-weight: 600;
  border-radius: 8px;
  background: white;
}
.link{
  margin-top: 20px;
}

.bot p {
  font-size: 15px;
  /* font-weight: 500; */
  text-align: left;
}

.bot img {
  width: 250px;
  border-radius: 10px;
  box-shadow: 0 1px 6px 0 rgb(32 33 36 / 28%);

}
.nav{
  position: fixed;
  padding: 20px;
  background: #343232;
  width: 100%;
  color: white;
  font-weight: 600;
  top: 0;
  z-index: 1;
}
.bi-search{
  font-size: 20px;
}